@import url(https://fonts.googleapis.com/css?family=Montserrat:100,200,300,400,500,600,700,800,900&display=swap);

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}



.studios {
    position: absolute;
    left: 20px;
    top: 20px;
    z-index: 2;
    display: grid;
    grid-gap: 10px;
    gap: 10px;
}

.studio {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-row-gap: 16px;
    row-gap: 16px;
    grid-column-gap: 12px;
    -webkit-column-gap: 12px;
       -moz-column-gap: 12px;
            column-gap: 12px;
    width: 320px;
    background-color: black;
    color: white; 
    padding: 20px;
    border-radius: 16px;  
    font-family: 'Montserrat', sans-serif; 
    text-align: left;
}

.studio * {
    margin: 0;
}

.studio-description {
    grid-column: 1 / -1;
    text-align: left;
}

.studio-heading {
    grid-column: 1 / -1;
    display: grid;
    grid-template-columns: 40px auto;
    grid-gap: 12px;
    gap: 12px;
    align-items: center;
}

.studio-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    
}

.studio-logo {
    -webkit-filter: invert();
            filter: invert();
    width: 40px;
}

.studio-images {
    grid-column: 1 / -1;
    display: grid;
    grid-auto-flow: column;
    grid-auto-rows: 80px;
    grid-gap: 12px;
    gap: 12px;
}

.studio-image {
    border-radius: 10px;
}


.call-to-action {
    grid-column: 1 / -1;
    display: grid;
    grid-template-columns: auto auto;
}

.name-and-availability {
    text-align: left;
}

.studio-description {
    font-size: 0.8rem;
}

.studio-name {
    font-size: 1rem;
}

.studio-availability {
    font-weight: 300;
    font-size: 0.825rem;
}

.studio-comment {   
    -webkit-transform: rotateY(180deg);   
            transform: rotateY(180deg); 
    margin-right: 8px;   
}


@-webkit-keyframes blinky {
    to {
        opacity: 0;
    }
}


@keyframes blinky {
    to {
        opacity: 0;
    }
}

.blink {

    display: inline-block;
    background-color: yellowgreen;
    width: 8px;
    height: 8px;
    margin-right: 8px;
    border-radius: 50%;
    -webkit-animation: blinky 1s infinite alternate;
            animation: blinky 1s infinite alternate;

}
.App {
  text-align: center;
  width: 100vw;
  height: 100vh;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.location-icon {
  width: 20px;
}

